body{
    background-color: rgba(255, 123, 0, 0.06);
    margin: 0;
}

button, input{
    padding: 0;
border: none;
}

.gallery{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
}

.photo-card{
    display: flex;
    flex-wrap:wrap;
    width: 320px;
    height: 320px;
    margin-bottom: 25px;
    border-radius: 5px;
    box-shadow: 8px 8px 25px -3px rgba(0,0,0,0.79);
}

.photo-card > img{
    width: 320px;
    height: 240px;
    object-fit: cover;
}

.info{
    display: flex;
    justify-content: space-evenly;
    flex-wrap:nowrap;
    width: 320px;
    height: 80px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: 300;
}

.info-item{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 80px;
    margin-top: 0;
    margin-bottom: 0;
    font-size: 11px;
}

.info-item--name{
    font-weight: 600;
}

.is-hidden{
    display: none;
}

.search-form{
    display: flex;
    justify-content: center;
}

.input-search{
    background-color: inherit;
    border-radius: 12px;
    box-shadow: 3px 3px 9px #666666;
    color: #000000;
    font-size: 14px;
    padding: 10px 20px 10px 20px;
    text-decoration: none;
    margin-right: 20px;
    margin-top: 20px;
  margin-bottom: 20px;
}

.load-more, .submit-btn {
  background: #0096fa;
  background-image: -webkit-linear-gradient(top, #0096fa, #044f7d);
  background-image: -moz-linear-gradient(top, #0096fa, #044f7d);
  background-image: -ms-linear-gradient(top, #0096fa, #044f7d);
  background-image: -o-linear-gradient(top, #0096fa, #044f7d);
  background-image: linear-gradient(to bottom, #0096fa, #044f7d);
  -webkit-border-radius: 28;
  -moz-border-radius: 28;
  border-radius: 12px;
  box-shadow: 3px 3px 9px #666666;
  color: #ffffff;
  font-size: 14px;
  padding: 10px 20px 10px 20px;
  text-decoration: none;
  margin-left: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.load-more:hover, .load-more:focus, .submit-btn:hover, .submit-btn:focus {
  background: #044f7d;
  background-image: -webkit-linear-gradient(top, #044f7d, #0096fa);
  background-image: -moz-linear-gradient(top, #044f7d, #0096fa);
  background-image: -ms-linear-gradient(top, #044f7d, #0096fa);
  background-image: -o-linear-gradient(top, #044f7d, #0096fa);
  background-image: linear-gradient(to bottom, #044f7d, #0096fa);
  box-shadow: 3px 3px 9px #666666;
  text-decoration: none;
}

.load-more--btn{
    display: flex;
    justify-content: center;
    align-items: center;
}
